export default{
	data(){
		return{
			"number" : 1
		}
	},
	methods:{
		//减少数量
		minus(){
			if(this.number<=1){
				return;
			}
			this.number--;
		    this.$emit('func' ,this.number)
		},
		//增加数量
		add(){
			this.number++;
			this.$emit('func' ,this.number)
		},
		a(){
			this.$emit('func' ,this.number)
		}
	}
}
